<template>
<div class= "websocket" >
  <h1>Hello websocket</h1>
</div>
</template>
<script>
// import { Socket } from 'net';
// websocket  http  
// long poll: response
//   send()
// () 
export default {
  data () {
    return {
    }
  },
  mounted () {
    let socket = new WebSocket('ws://localhost:9001')
    socket.onopen = (e) => {
      console.log(e, '')
    }
    socket.onmessage = (e) => {
      console.log(e)
      // if (!e) socket.close()
    }
    socket.onclose = (e) => {
      console.log('close')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



