<template>
    <div class="hello">
      <h1>Hello App!</h1>
      <button @click="handleCall"></button>
      <button @click="handleCY"></button>
      <h2>{{$route.params.id}}</h2>
      <!-- <h1>{{newMessage}}</h1> -->
      <!-- <router-view></router-view>
      <button @click="historyClick">history</button> -->
    </div>
</template>

<script>
// vue routervue.js(components)(routes).this.$routerthis.$route
//   route.params/query/hash
//  '$route' beforeRouteUpdate
export default {
  data () {
    return {
      msg: 'hello VueJS',
      newMessage: 'This is the test of vue-router',
      id: null
    }
  },
  // watch: {
  //   '$route' (to, from) {
  //     console.log(to, from)
  //   }
  // },
  //
  methods: {
    handleCall () {
      this.id = parseInt(Math.random() * 10)
      this.$router.push({name: 'mycomponent', params: {id: this.id}})
    },
    historyClick () {
      console.log(window.history)
      this.$router.go(-1)
    },
    handleCY () {
      this.$router.push({path: '/addrouter'})
    }
  },
  beforeRouteUpdate (to, from, next) {
    next()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



