<template>
  <!-- <div class="test">
    <div v-for="item in items" :key="item.slotName">
      <slot :name="item.slotName"></slot>
    </div>
  </div> -->
  <div class="flex-dialog">
    <div v-for="elItem in elItems" :key="elItem.label + Math.random(0, 1)" :style=" elItem.style || { width: '50%' }">
      <label>{{elItem.label}}</label>
      <span v-if="elItem.slotName" slot-scope="scope">
        <slot :name="elItem.slotName" v-bind="scope"></slot>
      </span>
      <span v-else>{{ showData[elItem['prop']] }}</span>
    </div>
  </div>
</template>
<script>
/**
 * VueAPI
 *    ('')  
 *  
 * :
 *  name  v-slot namedefault v-slot <template>
 *  v-slot
 *     v-slot
 * :  <slot>prop  <template>
 * prop  v-slotJavaScript 
 * prop/ prop prop/ propundefined
 * v-slot 
 *  v-slot (v-slot:)#     
 * prop prop 
 * slot  slot-scope
 * slot-scope<template> slot-scope prop slot-scopepropslotProps<template>
 * /slot-scope <template>() 
 *   <div class= "slot-component" >
    <!-- <a class="nav-link">
      <slot name="header" />
      <slot>submit</slot>
      <slot name="footer" />
    </a> -->
    <!-- <span>
      <slot :user="user"> {{ user.lastName }} </slot>
      <slot name="header" />
    </span> -->
  <!-- </div> -->
  <ul>
    <li
      v-for="todo in filteredTodos"
      :key="todo.id">
      <slot :name="items.slotName" :todo="todo">
        {{ todo.text }}
      </slot>
    </li>
  </ul>
  keep-alive:Vue   DOM/  
*/
export default {
  props: {
    items: {
      type: Array,
      default: () => []
    },
    elItems: Array,
    showData: Object
  },
  data () {
    return {
      user: {
        firstName: 'cy',
        lastName: 'wcx'
      },
      filteredTodos: [
        {
          id: 1,
          text: 's'
        },
        {
          id: 2,
          text: 'm'
        },
        {
          id: 3,
          text: 'd',
          isComplete: true
        }
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



